import * as monitoringService from "@/service/cycle-monitoring";

const state = () => ({
    details: null,
    lines: null,
    comments: null
});

const actions = {
    async getCycleMonitoringDetails({ state }, cycleId) {
        const {
            monitorDetails,
            monitorLines,
            monitorLineComments
        } = await monitoringService.getCycleMonitoringDetails(cycleId);

        state.details = monitorDetails;
        state.lines = monitorLines;
        state.comments = monitorLineComments;
    }
};

export default {
    namespaced: true,
    state,
    actions
};
