<template>
    <span class="p-ml-2 p-mr-2"> Cycle: </span>
    <span style="color: #f44336">{{ cycleId ? cycleId : "--------" }}</span>
    <p-divider layout="vertical" />
    <span style="color: #f44336">{{ cycleType ? cycleType : "-----" }}</span>
    <p-divider layout="vertical" />
    <span class="p-mr-2"> Patient: </span>
    <span style="color: #f44336">{{ patient ? patient : "----------" }}</span>
    <p-divider layout="vertical" />
    <span class="p-mr-2">Demographic No: </span>
    <span style="color: #f44336">
        {{ demographicNo ? demographicNo : "--------" }}
    </span>
    <p-divider layout="vertical" />
    <span style="color: #f44336">
        {{ dateOfBirth ? dateOfBirth : "----/--/--" }}
    </span>
    <p-divider layout="vertical" />
    <span style="color: #f44336">({{ age ? age : "--" }} yrs)</span>
    <p-divider layout="vertical" />
    <span style="color: #f44336">{{ doctor ? doctor : "--" }}</span>
</template>

<script>
import { computed, ref } from "vue";
import { emitter } from "@/event-bus";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();

        const cycleId = computed(() =>
            store.state.artCycle.selectedCycle
                ? store.state.artCycle.selectedCycle.cycleId
                : null
        );
        const cycleType = computed(() =>
            store.state.artCycle.selectedCycle
                ? store.state.artCycle.selectedCycle.cycleType
                : null
        );
        const patient = ref();
        const demographicNo = ref();
        const dateOfBirth = ref();
        const doctor = ref();
        const age = computed(() => {
            if (dateOfBirth.value === undefined) {
                return null;
            }
            return Math.floor(
                (new Date() - new Date(dateOfBirth.value).getTime()) /
                    3.15576e10
            );
        });

        emitter.on(
            "patient-search:patient-selected",
            ({
                demographicNo: demographic,
                firstName,
                lastName,
                dateOfBirth: _dateOfBirth,
                providerName,
            }) => {
                patient.value = `${firstName} ${lastName}`;
                demographicNo.value = demographic;
                doctor.value = providerName;
                dateOfBirth.value = _dateOfBirth;
            }
        );

        return {
            cycleId,
            cycleType,
            patient,
            demographicNo,
            dateOfBirth,
            age,
            doctor,
        };
    },
};
</script>
