<template>
    <p-toolbar class="p-text-bold">
        <template #left>
            <header-info />
        </template>

        <template #right>
            <p-button
                icon="pi pi-user"
                class="p-mr-2"
                @click="showUserOverlay"
            />
        </template>
    </p-toolbar>

    <p-tab-menu :model="menu" />

    <div id="main">
        <router-view />
    </div>

    <p-overlay-panel ref="userOverlay" style="width: 250px">
        <div class="p-mb-3">
            <p-avatar icon="pi pi-user" />
            User
        </div>
        <p-button label="Log Out" @click="logout()" />
    </p-overlay-panel>
</template>

<script>
import { ref } from "vue";
import { useStore } from "vuex";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import router from "@/router";

import HeaderInfo from "@/components/shared/HeaderInfo.vue";

export default {
    components: {
        HeaderInfo,
    },
    setup() {
        const store = useStore();

        const menu = ref([
            { to: "/art-cycle", label: "ART Cycle" },
            { to: "/cycle-monitoring", label: "Cycle Monitoring" },
            { to: "/opu", label: "OPU" },
            { to: "/semen", label: "Semen" },
            { to: "/semen-cryo-storage", label: "Semen Cryo Storage" },
            { to: "/pregnancy", label: "Pregnancy" },
            { to: "/fertilization", label: "Fertilization" },
            { to: "/embryo-transfer", label: "Embryo Transfer" },
            { to: "/cryo-storage", label: "Cryo Storage" },
            { to: "/blood-tests", label: "Blood Tests" },
            { to: "/daily-report", label: "Daily Report" },
            { to: "/cycle-report", label: "Cycle Report" },
            { to: "/patient-mas-log", label: "Patient Mas Log" },
            { to: "/message-inbox", label: "Message Inbox" },
            { to: "/born", label: "Born" },
            { to: "/test", label: "Test", visible: false },
        ]);

        const userOverlay = ref();
        const showUserOverlay = (event) => {
            userOverlay.value.toggle(event);
        };

        const logout = () => {
            store.dispatch("logout").then(() => {
                router.push({ name: "Login" });
            });
        };

        emitter.on(EVENT_STRINGS.LOGOUT, (data) => {
            console.log("logout from defalut");
        });

        return {
            menu,
            userOverlay,
            showUserOverlay,
            logout,
        };
    },
};
</script>

