import { $axios } from "./app-http";

const CYCLE_MONITORING_DETAIL_ENDPOINT = "cycle/monitoring-details";

export async function getCycleMonitoringDetails(cycleId) {
    const { data } = await $axios.get(CYCLE_MONITORING_DETAIL_ENDPOINT, {
        params: {
            cycleId
        }
    });

    return data;
}
